<template>
<div>
    <div class="table-responsive">
        <table class="table table-striped">
            <thead>
                <tr>
                    <th>Created</th>
                    <th>Email</th>
                    <th>List</th>
                    <th>Amount</th>
                    <th>Paid</th>
                </tr>
                </thead>
                <tbody>
                    <tr v-for="(item, index) in orders" :key="index">
                        <td>{{timeStamp2String(item.create_at)}}</td>
                        <td>{{item.user.email}}</td>
                        <td>
                            <span v-for="(product,index) in item.products" :key="index">
                                {{product.product.title}} * {{product.qty}}
                                <br>
                            </span>
                        </td>
                        <td>{{item.total}}</td>
                        <td v-if="item.is_paid" class="text-success">已付款</td>
                        <td v-else class="text-danger">尚未付款</td>
                    </tr>
                </tbody>
        </table>
    </div>
    <paginationCom :pagination="pagination"></paginationCom>
    
</div>
</template>

<script>
import paginationCom from "@/components/pagination.vue"

export default {
    data() {
        return {
           orders: [],
           pagination: {}, 
           isLoading: false,
        }
    },
    components: {
        paginationCom,
    },
    methods: {
        getOrder(page = 1) {
            const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_USER}/admin/orders?page=${page}`;
            console.log("begin to get orders")
            console.log(api)
            var vm = this;
            vm.isLoading = true
            vm.$http.get(api).then(response => {
                console.log("response data")
                console.log(response)
                if (response.data.success) {
                    vm.orders = response.data.orders;
                    vm.pagination = response.data.pagination;
                } else {
                    console.log("Get orders failed")
                }
                vm.isLoading = false
            })
        },
        timeStamp2String(time) {
            var datetime = new Date();
            datetime.setTime(time*1000);
            var year = datetime.getFullYear();
            var month = datetime.getMonth() + 1;
            var date = datetime.getDate();
            var hour = datetime.getHours();
            var minute = datetime.getMinutes();
            var second = datetime.getSeconds();
            return year + "-" + month + "-" + date+" "+hour+":"+minute+":"+second
        }
    },
    created() {
        this.getOrder()
    },
    mounted() {
        this.$on('refreshProduct',this.getOrder);
    },
}
</script>